<template>
  <div>
    <!--  -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title="" class="navTitle"></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div v-for="(item, index) in list" :key="index">
          <!--  -->
          <div class="chat-item left" v-if="item.name !== 'me'">
            <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
            <div class="chat-pao">{{item.msg}}</div>
          </div>

          <!--  -->
          <div class="chat-item right" v-else>
            <div class="chat-pao-right">{{item.msg}}</div>
            <van-image fit="cover" round :src="photo" />
          </div>
       </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field  placeholder="..." v-model="words" @keyup.enter="sendFn">
        <template #button>
          <span  style="font-size:12px;color:#000" @click="sendFn"></span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
import { io } from 'socket.io-client'
export default {
  name: 'Chat',
  data () {
    return {
        words : '',
        list: [
            //  name 
            { name: 'xs', msg: 'hi' },
            { name: 'me', msg: '' }
        ],
        socket : null,    // 
    }
  },
  computed: {
    photo() {
        return this.$store.state.userInfo.photo
    }
  },
  created () {
    // ioiosocketaxios

    // socket
    this.socket = io('http://toutiao.itheima.net', {
        query: {
            token: this.$store.state.user
        },
        transports: ['websocket']
    })

    //  connect
    this.socket.on('connect', ()=> {
        console.log('webSocket')
    })

    // 
    this.socket.on('message', data => {
        this.list.push({
            name : 'xs',
            msg : data.msg
        })

        this.$nextTick(() => {
            this.scrollViewBottom()
        })
    })
  },
  methods : {
    // 
    sendFn() {
        if (this.words.trim().length === 0) return

        // 
        this.list.push({
            name : 'me',
            msg : this.words
        })

        this.socket.emit('message', {
            msg : this.words,
            timestamp : new Date().getTime()
        })

        // words
        this.words = ''

        this.$nextTick(() => {
            this.scrollViewBottom()
        })
    },
    scrollViewBottom() {
        let chatItem = document.querySelectorAll('.chat-item')
        // 
        let lastItem = chatItem[chatItem.length - 1]
        lastItem.scrollIntoView({
            behavior : 'smooth'    // 
        })
    },
  },
  destroyed() {
    this.socket.close()  // socket
    this.socket = null   // 
  }
}
</script>

